import React, { useEffect, useState } from "react";
import { useLocation } from "react-router";
import Scribble from "./ScribbleNote";
import yearbook from "../yearbook.json";
import axios from "../api";
import "./User.css";
import Loader from "./Loader";

export default function User() {
	const [loading, setLoading] = useState(true);
	const [user, setUser] = useState({});
	const [data, setData] = useState([]);

	const location = useLocation();
	useEffect(() => {
		setLoading(true);
		const roll = new URLSearchParams(location.search.substr(1)).get("roll");
		yearbook.forEach((entry) => {
			if (entry.roll === roll) setUser(entry);
		});
		axios
			.get(`/posts`)
			.then(({ data }) => {
				if (data?.length) {
					setData(data.filter((entry) => entry.roll === roll));
				}
			})
			.catch(alert)
			.finally(() => setLoading(false));
	}, [location]);

	if (loading) return <Loader />;
	const { name, nickname, line, about, roll } = user;
	return (
		<div className="wall">
			<h1>{name}</h1>
			<div className="yb-card">
				<img
					src={`yb_photos/${roll}.jpg`}
					alt="profile"
					className="profile-picture"
				/>
				<div className="description">
					<p className="nickname">{nickname}.</p>
					<p className="quote">{line}</p>
					<p className="about">{about}</p>
				</div>
			</div>
			<div className="divider" />
			<h1>Wall üß±</h1>
			<div className="holder">
				{data.map((entry) => {
					return <Scribble {...entry} />;
				})}
			</div>
			<p className="preview-text">
				Write a scribble on this wall
				<a href={`?component=scribble&init_roll=${roll}`}> here!</a>
			</p>
				<p className="preview-text">
					For assistance/queries reachout to{" "}
					<a
						href="mailto:wellbeing@iitj.ac.in"
						target="_blank"
						rel="noreferrer"
					>
						us
					</a>
					{" "}or mail{" "}
					<a
						href="mailto:tawatia.1@iitj.ac.in"
						target="_blank"
						rel="noreferrer"
					>
						dev
					</a>
					{" "}to report.
				</p>
		</div>
	);
}
